body {
  position: fixed;
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-position: center;
  background-attachment: fixed;
  z-index: -1;
}

.employee-page {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
}

.employee-container {
  display: flex;
  align-items: center;
  text-align: left;
}

.employee-container img {
  width: 100px;
  height: 100px;
}

.employee-logo {
  padding: 5px 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.employee-button1 {
  width: 240%;
  padding: 0.75rem;
  margin-top: 250%;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  background-color: #f5f5f5;
  color: rgb(0, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem; 
  margin-left: -260%; 
  transition: transform 0.3s, border 0.3s;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: large;
}

.employee-button1:hover {
  transform: scale(1.05);
}

