.employee-profile {
  display: flex;
  align-items: center;
  font-size: 30px;
  color: #DBE1CD;
}

.employee-profile-container:hover {
  background-color: #5E60E0;
  border-radius: 40%;
}

.employee-profile img {
  margin-left: 788%;
  width: 50px;
  height: 55px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #ddd;
  box-shadow: 0 4px 8px rgba(63, 52, 52, 0.1);
}

.employee-profile h2 {
  font-size: 18px;
  margin-top: 10;
}

.employee-profile:hover {
  transform: scale(1.15); 
}


.employee-slide {
  transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -webkit-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -moz-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -o-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  transform: translateX(100%);
  -webkit-transform: translateX(100%);
  -moz-transform: translateX(100%);
  -o-transform: translateX(100%);
}

.employee-slide-in {
  transform: translateX(0%);
  /* margin-left: 10%; */
}


.employee-slide {
  transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  transform: translateX(100%);
}

.employee-slide-in {
  transform: translateX(0%);
}